function Badge({ children }: { children: React.ReactNode }) {
  return (
    <span
      style={{
        display: "inline-flex",
        alignItems: "center",
        gap: 8,
        padding: "6px 10px",
        borderRadius: 999,
        border: "1px solid var(--tl-border)",
        background: "var(--tl-surface)",
        fontSize: 12,
        opacity: 0.92,
        whiteSpace: "nowrap",
      }}
    >
      {children}
    </span>
  );
}

function Chip({ children }: { children: React.ReactNode }) {
  return (
    <span
      style={{
        display: "inline-flex",
        alignItems: "center",
        padding: "5px 10px",
        borderRadius: 999,
        border: "1px solid var(--tl-border)",
        background: "rgba(var(--tl-accent), 0.06)",
        fontSize: 12,
        fontWeight: 800,
        opacity: 0.9,
        whiteSpace: "nowrap",
      }}
    >
      {children}
    </span>
  );
}

function SectionHeader({
  title,
  subtitle,
  right,
}: {
  title: string;
  subtitle?: string;
  right?: React.ReactNode;
}) {
  return (
    <div
      style={{
        display: "flex",
        alignItems: "flex-end",
        justifyContent: "space-between",
        gap: 14,
        flexWrap: "wrap",
        marginBottom: 12,
      }}
    >
      <div style={{ display: "grid", gap: 6 }}>
        <div style={{ fontSize: 18, fontWeight: 950, letterSpacing: -0.2 }}>{title}</div>
        {subtitle ? <div style={{ fontSize: 13, opacity: 0.72 }}>{subtitle}</div> : null}
      </div>
      {right ? <div style={{ display: "flex", gap: 8, flexWrap: "wrap" }}>{right}</div> : null}
    </div>
  );
}

function FancyStep({
  n,
  title,
  text,
  accent = false,
}: {
  n: string;
  title: string;
  text: string;
  accent?: boolean;
}) {
  return (
    <div
      style={{
        position: "relative",
        padding: 16,
        borderRadius: 18,
        border: `1px solid ${accent ? "rgba(var(--tl-accent), 0.26)" : "var(--tl-border)"}`,
        background: accent
          ? "radial-gradient(900px 220px at 15% 10%, rgba(var(--tl-accent), 0.14), transparent 55%), var(--tl-surface)"
          : "var(--tl-surface)",
        boxShadow: "inset 0 1px 0 rgba(255,255,255,0.06)",
        display: "grid",
        gap: 10,
        transition: "transform 160ms var(--tl-ease), border-color 160ms var(--tl-ease)",
      }}
      onMouseEnter={(e) => {
        (e.currentTarget as HTMLDivElement).style.transform = "translateY(-2px)";
        (e.currentTarget as HTMLDivElement).style.borderColor = accent
          ? "rgba(var(--tl-accent), 0.34)"
          : "var(--tl-border-strong)";
      }}
      onMouseLeave={(e) => {
        (e.currentTarget as HTMLDivElement).style.transform = "translateY(0px)";
        (e.currentTarget as HTMLDivElement).style.borderColor = accent
          ? "rgba(var(--tl-accent), 0.26)"
          : "var(--tl-border)";
      }}
    >
      <div style={{ display: "flex", alignItems: "center", gap: 10 }}>
        <div
          style={{
            width: 34,
            height: 34,
            borderRadius: 12,
            display: "grid",
            placeItems: "center",
            fontWeight: 950,
            border: "1px solid var(--tl-border)",
            background: "rgba(0,0,0,0.20)",
            boxShadow: "inset 0 1px 0 rgba(255,255,255,0.06)",
          }}
        >
          {n}
        </div>

        <div style={{ display: "grid", gap: 2 }}>
          <div style={{ fontWeight: 950, letterSpacing: -0.2 }}>{title}</div>
          <div style={{ fontSize: 12, opacity: 0.65 }}>{accent ? "Recommended flow" : "Step"}</div>
        </div>

        <div style={{ marginLeft: "auto", display: "flex", gap: 8, flexWrap: "wrap" }}>
          {n === "1" ? <Chip>~30 sec</Chip> : null}
          {n === "2" ? <Chip>Context</Chip> : null}
          {n === "3" ? <Chip>Stats</Chip> : null}
        </div>
      </div>

      <div style={{ opacity: 0.78, fontSize: 13, lineHeight: 1.55 }}>{text}</div>
    </div>
  );
}

function FeatureCard({
  icon,
  title,
  subtitle,
  points,
  chips,
}: {
  icon: string;
  title: string;
  subtitle: string;
  points: string[];
  chips?: string[];
}) {
  return (
    <div
      style={{
        position: "relative",
        borderRadius: 22,
        border: "1px solid var(--tl-border)",
        background:
          "radial-gradient(900px 240px at 10% 10%, rgba(var(--tl-accent), 0.10), transparent 55%), var(--tl-surface)",
        padding: 18,
        boxShadow: "inset 0 1px 0 rgba(255,255,255,0.06)",
        transition: "transform 160ms var(--tl-ease), border-color 160ms var(--tl-ease)",
      }}
      onMouseEnter={(e) => {
        (e.currentTarget as HTMLDivElement).style.transform = "translateY(-2px)";
        (e.currentTarget as HTMLDivElement).style.borderColor = "var(--tl-border-strong)";
      }}
      onMouseLeave={(e) => {
        (e.currentTarget as HTMLDivElement).style.transform = "translateY(0px)";
        (e.currentTarget as HTMLDivElement).style.borderColor = "var(--tl-border)";
      }}
    >
      <div style={{ display: "flex", alignItems: "center", gap: 10, marginBottom: 10 }}>
        <div
          style={{
            width: 40,
            height: 40,
            borderRadius: 14,
            display: "grid",
            placeItems: "center",
            border: "1px solid var(--tl-border)",
            background: "rgba(0,0,0,0.18)",
            boxShadow: "inset 0 1px 0 rgba(255,255,255,0.06)",
            fontSize: 18,
          }}
        >
          {icon}
        </div>

        <div style={{ display: "grid", gap: 2, minWidth: 0 }}>
          <div style={{ fontSize: 18, fontWeight: 950, letterSpacing: -0.2 }}>{title}</div>
          <div style={{ fontSize: 13, opacity: 0.7 }}>{subtitle}</div>
        </div>
      </div>

      {chips?.length ? (
        <div style={{ display: "flex", gap: 8, flexWrap: "wrap", marginBottom: 12 }}>
          {chips.map((c) => (
            <Chip key={c}>{c}</Chip>
          ))}
        </div>
      ) : null}

      <ul style={{ margin: 0, paddingLeft: 18, display: "grid", gap: 10, opacity: 0.9 }}>
        {points.map((p) => (
          <li key={p} style={{ lineHeight: 1.55 }}>
            {p}
          </li>
        ))}
      </ul>
    </div>
  );
}

